* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  color: #fafafa;
  background-color: #2a3e54;
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

#search-box {
  font: inherit;
  width: 320px;
  text-align: center;
  border: 2px solid #939392;
  border-radius: 10px;
  margin: 50px auto 16px;
  padding: 8px;
  transition-property: width;
  transition-duration: .1s;
  transition-timing-function: linear;
  display: block;
}

#search-box:focus {
  width: 450px;
  border: 2px solid #14a5a5;
  outline: none;
}

.country-list {
  max-width: 90%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding: 0;
  font-weight: 700;
  list-style: none;
  display: flex;
}

.country-list__card {
  min-width: 450px;
  flex-basis: calc(50% - 15px);
  align-items: center;
  gap: 10px;
  display: flex;
}

.country-list__card:hover {
  cursor: pointer;
}

.country-list__img {
  min-width: 100px;
  height: 100%;
  transition: transform .25s linear;
  display: block;
}

.country-list__card:hover .country-list__img {
  transform: scale(1.15)rotate(-3deg);
}

.img-wrap {
  width: 100px;
  height: 70px;
}

.country-list__img {
  object-fit: contain;
  max-width: 100%;
}

.country-list__desc {
  padding-bottom: 5px;
  font-size: 20px;
  transition: font-size .25s linear;
  position: relative;
}

.country-list__desc:after {
  content: "";
  height: 1px;
  width: 0;
  background-color: #fff;
  margin: auto;
  transition: width .25s linear;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.country-list__card:hover .country-list__desc:after {
  width: 100%;
}

.country-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.country-info__img {
  min-height: 300px;
  width: 450px;
  max-height: 350px;
  border-radius: 10px;
}

.country-info__name {
  margin: 20px 0;
  font-size: 50px;
}

.country-info__cap, .country-info__pop, .country-info__lan {
  margin: 7px 0;
}

